
  
  $font-1: "font-1", sans-serif;
  $font-2: "font-2", sans-serif;
  
  $color-1: #546fe4;
  $color-2: #212040;
  $color-3: rgb(100, 100, 100);
  $black: #060d2c;
  $white: rgb(238, 238, 238);
  
