$color-1: #546fe4;
$black: black;
.form-component {
    .form-container {
      text-align: center;
    }
    form {
      width: 300px;
      margin: 0 auto;
  
      input[type="text"] {
        padding: 1px 10px;
        border-radius: 10px ;
        border: none;
        font-size: 1.5rem;
        display: block;
        margin: 0 auto;
        margin-bottom: 5px;
        width: 220px;
        outline: none;
        text-align: center;
      }
      input[type="submit"] {
        padding: 1px 10px;
        width: 220px;
        border-radius: 0 0 10px 10px;
        border: none;
        background: $color-1;
        color: white;
        font-size: 1.5rem;
        cursor: pointer;
        z-index: 10;
        position: relative;
  
        &:hover {
          background: black;
        }
      }
    }
   h2{
       text-align: center;
       margin-top: 5px;
       margin-bottom: 5px;
   }
  }
  